<template>
  <div class="table-wrap">
    <base-table class="journal-data-table" v-bind="$attrs" style="min-width: 1200px;" :list="tableList">
      <template slot="header" slot-scope="i">
        <th>凭证日期</th>
        <th>凭证年</th>
        <th>凭证月</th>
        <th>凭证号</th>
        <th>摘要</th>
        <th>科目编码</th>
        <th>借贷</th>
        <th>金额</th>
        <th>成本中心编码</th>
        <th>订单编号</th>
        <th>公司编码</th>
      </template>
      <template>
        <td></td>
        <td></td>
        <td></td>
        <td></td>
        <td></td>
        <td></td>
        <td></td>
        <td></td>
        <td></td>
        <td></td>
        <td></td>
      </template>
    </base-table>
    <base-pager :count="10"></base-pager>
  </div>

</template>
<script>
export default {
  props: {
    inheritAttrs: false
  },
  data () {
    return {
      tableList: [{}]
    }
  },
  beforeMount () {
    this.$services.manage.getJournalDataList()
  },
  methods: {}
}
</script>
<style lang="scss" scoped>
</style>



